                    {{ generatedContent.analysis }}
                  </div>
                  <div v-else class="placeholder">
                    <el-icon class="placeholder-icon"><TrendCharts /></el-icon>
                    <p>分析生成中...</p>
                  </div>
                </div>
              </div>
