    </el-container>

    <!-- 底部提示区域 -->
    <div class="suggestion-area">
      <div class="suggestion-buttons">
        <el-button class="suggestion-btn" @click="fillExample('outdoor')">
          能帮我写一篇关于春天户外郊游的文案吗？
        </el-button>
        <el-button class="suggestion-btn" @click="fillExample('skincare')">
          我需要一条关于日常护肤分享的文案。
        </el-button>
        <el-button class="suggestion-btn" @click="fillExample('fitness')">
          请为我做一条健身日记的文案内容。
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Magic, DocumentCopy, Document, Star, PriceTag, TrendCharts } from '@element-plus/icons-vue'
import axios from 'axios'

export default {
  name: 'App',
  components: {
    Magic,
    DocumentCopy,
    Document,
    Star,
    PriceTag,
    TrendCharts
  },
  setup() {
    const activeTab = ref('note')
    const loading = ref(false)

    const productForm = reactive({
      name: '',
      category: '',
      price: '',
      description: '',
      targetUser: '',
      scenario: ''
    })

    const generatedContent = reactive({
      note: '',
      titles: [],
      tags: [],
      analysis: ''
    })

    // 流式生成内容
    const generateContentStream = async () => {
      if (!productForm.name || !productForm.description) {
        ElMessage.warning('请填写产品名称和描述')
        return
      }

      loading.value = true
      
      // 清空之前的内容
      generatedContent.note = ''
      generatedContent.titles = []
      generatedContent.tags = []
      generatedContent.analysis = ''

      try {
        const response = await fetch('http://localhost:8080/api/notes/generate-stream', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            name: productForm.name,
            description: productForm.description,
            category: productForm.category,
            price: parseFloat(productForm.price.replace(/[^\d.]/g, '')) || 0,
            targetUser: productForm.targetUser,
            scenario: productForm.scenario
          })
        })

        if (!response.ok) {
          throw new Error('网络请求失败')
        }

        const reader = response.body.getReader()
        const decoder = new TextDecoder()
        let buffer = ''

        let done = false
        while (!done) {
          const result = await reader.read()
          done = result.done
          if (done) break
          const value = result.value

          buffer += decoder.decode(value, { stream: true })
          
          // 处理流式数据，逐字显示
          const lines = buffer.split('\n')
          buffer = lines.pop() // 保留最后一个不完整的行
          
          for (const line of lines) {
            if (line.startsWith('data: ')) {
              const content = line.slice(6)
              if (content.trim()) {
                generatedContent.note += content
              }
            }
          }
        }

        // 处理剩余的buffer
        if (buffer.startsWith('data: ')) {
          const content = buffer.slice(6)
          if (content.trim()) {
            generatedContent.note += content
          }
        }

        ElMessage.success('生成完成！')

      } catch (error) {
        console.error('流式API调用失败:', error)
        ElMessage.error('生成失败，请检查后端服务是否启动')
      } finally {
        loading.value = false
      }
    }

    // 传统生成内容（保留作为备选）
    const generateContent = async () => {
      if (!productForm.name || !productForm.description) {
        ElMessage.warning('请填写产品名称和描述')
        return
      }

      loading.value = true

      try {
        // 调用后端API
        const response = await axios.post('http://localhost:8080/api/notes/generate', {
          name: productForm.name,
          description: productForm.description,
          category: productForm.category,
          price: parseFloat(productForm.price.replace(/[^\d.]/g, '')) || 0,
          targetUser: productForm.targetUser,
          scenario: productForm.scenario
        })

        // 更新生成的内容
        const data = response.data
        generatedContent.note = data.note
        generatedContent.titles = data.titles || []
        generatedContent.tags = data.tags || []
        generatedContent.analysis = data.analysis

        ElMessage.success('生成成功！')

      } catch (error) {
        console.error('API调用失败:', error)
        ElMessage.error('生成失败，请检查后端服务是否启动')
      } finally {
        loading.value = false
      }
    }

    // 复制内容
    const copyContent = async (content) => {
      try {
        await navigator.clipboard.writeText(content)
        ElMessage.success('复制成功')
      } catch (error) {
        ElMessage.error('复制失败')
      }
    }

    // 填充示例
    const fillExample = (type) => {
      switch (type) {
        case 'outdoor':
          Object.assign(productForm, {
            name: '春季踏青套装',
            category: 'fashion',
            price: '199-299元',
            description: '轻薄透气的春季外套，配上舒适的运动鞋，完美的踏青装备。面料柔软亲肤，版型修身显瘦。',
            targetUser: '20-35岁热爱户外的女性',
            scenario: '春季踏青、户外运动、日常休闲'
          })
          break
        case 'skincare':
          Object.assign(productForm, {
            name: '玻尿酸保湿精华',
            category: 'beauty',
            price: '89-129元',
            description: '高浓度玻尿酸配方，深层补水保湿，质地清爽不粘腻，适合各种肌肤类型。坚持使用肌肤水润有光泽。',
            targetUser: '18-40岁注重护肤的女性',
            scenario: '日常护肤、干燥季节、熬夜后修复'
          })
          break
        case 'fitness':
          Object.assign(productForm, {
            name: '瑜伽垫套装',
            category: 'sports',
            price: '99-159元',
            description: 'TPE环保材质，防滑耐磨，厚度适中提供良好支撑。配送瑜伽带和收纳袋，居家健身必备。',
            targetUser: '喜欢居家健身的人群',
            scenario: '居家瑜伽、健身训练、拉伸运动'
          })
          break
      }
    }

    return {
      activeTab,
      loading,
      productForm,
      generatedContent,
      generateContent,
      generateContentStream,
      copyContent,
      fillExample
    }
  }
}
</script>

